<template>
<transition name="slide-left-menu">
  <div class="_full_router">

    <div class="login-header normal-box">
      <div class="top-back" @click="goBack">
        <span class="mint-button-icon"><i class="mintui mintui-back"></i></span>
      </div>

      <div class="center">
        <div class="search-input"> <input type="text" placeholder="商品名称" v-model="searchInput" /><i class="mintui mintui-field-error" v-show="!showHistroy" @click="clearSearchInput"></i></div>

      </div>
      <div @click="search"><span class="mint-button-icon"><i class="mintui mintui-search"></i></span><span style="font-size:12px;">搜索</span></div>

    </div>
    <section class=" app-content">
      <div class="histroy-contain" v-show="showHistroy">
        <div>历史搜索 <span class="mint-button-icon"><i class="mintui mintui-delet"></i></span></div>
        <ul class="histroy-list">
          <li v-for="item in  histroyList">
            <span>{{item}}</span>
          </li>
        </ul>
        <div class="clear-btn"><button type="button" @click="clearHistroy" class="clear-histroy" v-show="histroyList.length!=0">清除历史记录</button></div>
      </div>
      <div class="result-contain" v-show="resultList.length!=0">
        <ul class="my-hold-container">
          <li v-for="(item,index) in resultList" >
            <div>
              <div>{{item.xxzqdm}}</div>
            </div>
            <div class="text-right">
              <div>{{item.xxzqjc}}</div>
            </div>
          </li>
        </ul>
      </div>
    </section>
    <footer class="app-footer _line-fine">
      <div class="_effect ">
        <indexNav></indexNav>
      </div>
    </footer>
  </div>
</transition>
</template>
<script type="text/javascript">
import indexNav from 'components/index-nav-shop.vue'

import cookieUtil from 'js-cookie'
export default {
  data() {
    return {
      searchInput: "",
      histroyList: [],
      resultList:[],
    }
  },
  computed: {
    showHistroy: function() {
      return this.searchInput == '';
    }
  },
  created: function() {
    var searchList = cookieUtil.get("searchList");
    if(searchList){
        this.histroyList = JSON.parse(searchList).datas;
    }else{
        this.histroyList=[];
    }

  },
  components: {
    indexNav,

  },
  methods: {
    clearHistroy: function() {
      cookieUtil.remove("searchList");
      this.histroyList = [];
    },
    clearSearchInput: function() {
      this.searchInput = "";
    },
    goBack: function() {
      this.$router.go(-1)
    },
    search: function() {
        if(this.searchInput!==""){
            var searchList = cookieUtil.get("searchList");
            if (searchList) {
              var datas = JSON.parse(searchList).datas;
              datas.push(this.searchInput);
              cookieUtil.set("searchList", {
                datas: datas
              })
            } else {
              cookieUtil.set("searchList", {
                datas: [this.searchInput]
              })
            }
            var searchList = cookieUtil.get("searchList");
            this.histroyList = JSON.parse(searchList).datas;
        }

      this.$router.push('shopMore')
    },
  }
}
</script>
<style type="text/css" scoped>
._full_router {
  transition: .5s all ease;
}

.login-header {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 3;
  height: 45px;
  line-height: 45px;
  font-size: 19px;
  background: #ef4f4f;
  color: #ffffff;
  text-align: center;
}

.top-back {
  max-width: 85px;
  float: left;
}

.histroy-contain {
  padding: 12px;
  font-size: 14px;
  color: #666;
}

.histroy-list {
  padding: 6px 0;
  overflow: hidden;
}

.histroy-list li {
  margin: 0 12px 12px 0;
  float: left;
}

.histroy-list span {
  padding: 4px 6px;
  border-radius: 18px;
  background: #e1e1e1;
}

.clear-btn {
  text-align: center;
}

.clear-btn button {

  background: rgba(0, 0, 0, 0);
  border: none;
  text-decoration: underline;
  color: #f11;
}

.login-header .search-input input {
  width: 70vw;
  height: 28px;
  border: none;
  border-radius: 4px;
  padding-left: 6px;
}

.search-input {
  position: relative;
}

.search-input .mintui-field-error {
  position: absolute;
  right: 10px;
  top: 4px;
  color: #aaa;
}

.btn {
  width: 49%;
  background-color: #fff;
  border: 0;
  height: 40px;
  font-size: 20px;
  outline: 0;
}
</style>



