	<template>
		<nav class="nav">
        <dl v-for="item in index_nav" @click="goNavPath(item)" :class="{active:item.active}"  >
        <router-link :to="item.path">

            <dt class="iconfont" >
                <!-- <i v-if="item.hint.count" v-text="item.hint | get_prompt " :class="'_news-'+item.hint.type"></i> -->
            </dt>
            <dd v-text="item.text"></dd>
          </router-link>
        </dl>
    </nav>
	</template>
<script>
export default{

	data:function(){
	return {
			index_nav:[{text:"首页" ,path: {
                            path: '/shop/index'
                                        },
                             active:true,
                        },
                        // {text:"分类", path: {
                        //     path: '/shop/classify'
                        // 	},
                        //      active:false,
                        // },
                         {text:"购物车" ,path: {
                            path: '/shop/cart'
                                        }
                        },{text:"账户" ,path: {
                            path: '/shop/personal'
                                        },
                             active:false,
                        },

                        ]
	   }
	},
    created:function(){
        // this.matchPath(window.location.hash);
        // global.router.beforeEach((to,from,next)=>{
        //    this.matchPath(to.fullPath);
        //     next();
        // })
    },
    methods:{
        matchPath:function(path){
             if(RegExp('^#?'+this.index_nav[0].path.path).test(path)){
                this.selectNav(this.index_nav[0]);
            }else if(RegExp('^#?'+this.index_nav[1].path.path).test(path)){
                this.selectNav(this.index_nav[1]);
            }else if(RegExp('^#?'+this.index_nav[2].path.path).test(path)){
                this.selectNav(this.index_nav[2]);
            }
            else if(RegExp('^#?'+this.index_nav[3].path.path).test(path)){
                this.selectNav(this.index_nav[3]);
            }
             else if(RegExp('^#?'+this.index_nav[4].path.path).test(path)){
                this.selectNav(this.index_nav[4]);
            }
        },
        goNavPath:function(item){

            //this.$router.push(item.path);
        },

        selectNav:function(item){
             if(!item.active) {
                this.index_nav.forEach(item=>{
                item.active=false;
            })
            item.active=true;
            }
        }

    }
}

</script>
    <style scoped>
            .nav dl,.nav a{
                    text-decoration-line: none;
                    color:#333;
            }
            .nav dl.active{
            color: #26aded;
           }
           .nav a.router-link-active{
            color: #26aded;
           }
    </style>



